<template>
  <div id="big_box">
    <!-- 线上 -->
    <div
      id="center"
      v-if="statusInfo"
    >
      <div
        id="customer"
        v-if="statusInfo.orderAddSource == 3 && statusInfo.payment == 0"
        class="bgf60"
      >顾客扫码付款 设置开门密码</div>
      <div
        v-else
        id="customer"
        class='bggreen'
      >顾客扫码 设置开门密码</div>
      <div id="code">
        <p v-if="statusInfo.orderAddSource == 3">
          &yen;{{ Number(statusInfo.totalPrice).toFixed(2) }}
          <span
            v-if="statusInfo.payment == 0"
            style="color:#ff7d41;"
          >(未支付)</span>
          <span
            v-else-if="statusInfo.payment <= 4"
            style="color:#2E7BF7;"
          >(线上已收款)</span>
          <span
            v-else-if="statusInfo.payment > 4"
            style="color:#2E7BF7;"
          >(线下已收款)</span>
        </p>
        <img
          :src="pic_code"
          alt
        />
        <div>
          <img
            src="@/assets/two/weix.png"
            class="w80"
          />
          <img
            src="@/assets/two/zhifb.png"
            class="w80"
          />
        </div>
      </div>
      <div
        id="ul"
        v-if="statusInfo.orderAddSource == 3"
      >
        <div
          v-for="(item,i) in fore"
          :key="i"
          :style="{'width':comWidth}"
        >
          <span>
            <span class="span"></span>
            {{ item }}
          </span>
        </div>
      </div>
      <!-- 返回首页 -->
      <div id="backIndex">
        <div
          @click="goBackIndex"
          v-if="statusInfo.orderAddSource == 3"
        >查看订单</div>
        <div
          @click="goOutLine"
          v-if="statusInfo.payment == 0 && statusInfo.orderAddSource == 3 && is_businessPayOffline == true"
        >
          <img
            src="@/assets/two/outlint_icon.png"
            alt
          />
          其他收款方式
        </div>
      </div>
      <div class="c_bottom">
        <div
          v-if="info.scaleName"
          class="c_b_box"
        >
          <div>房型</div>
          <div>{{ info.scaleName }}</div>
        </div>
        <div
          v-if="info.roomSn"
          class="c_b_box"
        >
          <div>房间号</div>
          <div>{{ info.roomSn }}</div>
        </div>
        <div class="c_b_box">
          <div>顾客姓名</div>
          <div>{{ statusInfo.roomUser?statusInfo.roomUser:'暂无' }}</div>
        </div>
        <div class="c_b_box">
          <div>入离时间</div>
          <div>{{ statusInfo.startTime | dataFilter('MM-DD') }}至{{ statusInfo.endTime  | dataFilter('MM-DD')}} <span v-if="statusInfo.orderType < 3 && statusInfo.orderType > 4">共{{ statusInfo.diffDay }}晚</span></div>
        </div>
        <div
          class="c_b_box"
          v-if="statusInfo.orderAddSource == 2"
        >
          <div>房间数</div>
          <div>{{ statusInfo.roomNum }}间</div>
        </div>
        <div
          class="c_b_box"
          v-if="statusInfo.orderAddSource == 2 && orderSourceMap"
        >
          <div>订单来源</div>
          <div>{{ orderSourceMap[statusInfo.orderSource] }}</div>
        </div>
        <div
          class="c_b_box"
          v-if="statusInfo.orderAddSource == 2"
        >
          <div>订单号</div>
          <div>{{ statusInfo.externalOrderSn }}</div>
        </div>
        <div
          class="c_b_box"
          v-if="statusInfo.orderAddSource != 2"
        >
          <div>房费</div>
          <div
            class="c_f60"
            v-if="statusInfo.orderType == '2' || statusInfo.orderType == '3' || statusInfo.orderType == '4'"
          >&yen;{{ Number(statusInfo.totalPrice - statusInfo.deposit).toFixed(2) }}</div>
          <div
            class="c_f60"
            v-else
          >&yen;{{ statusInfo.preOrderRoomId > '0' ? Number(statusInfo.totalPrice):Number(statusInfo.totalPrice - statusInfo.deposit).toFixed(2) }}</div>
        </div>
        <div
          class="c_b_box"
          v-if="statusInfo.orderAddSource != 2"
        >
          <div>押金</div>
          <div class="c_0">&yen;{{ statusInfo.deposit }}</div>
        </div>
        <div
          class="c_b_box c_0 f9"
          v-if="statusInfo.orderAddSource != 2"
        >
          <div class>总计</div>
          <div>&yen;{{ Number(statusInfo.totalPrice).toFixed(2) }}</div>
        </div>
      </div>
    </div>
    <!-- 线下收款 -->
    <mt-popup
      v-model="show_payment_outline"
      class="outline"
    >
      <div>线下收款</div>
      <div class="pay">
        <span></span>
        <span>收款账户</span>
        <span></span>
      </div>
      <div class="pay_title">
        <div
          v-for="(item,i) in payMentAll"
          :key="i"
          id="colorPay"
          @click="choose_payment(item,i)"
        >{{ item.accountName }}
        </div>
        <div style='background:#E7E8F1;'></div>
      </div>
      <div
        v-if="statusInfo.accountUnitId && statusInfo.accountUnitId > 0"
        class="user-style"
        style="font-weight:normal;"
        @click="payeeAccountIdValueShow = true"
      >
        <div class="w30">收款账户</div>
        <div>{{ payeeAccountIdValue }}</div>
      </div>
      <label>
        <img
          src="@/assets/two/icon_phone.png"
          alt
        />
        <input
          type="tel"
          maxlength="11"
          placeholder="请输入顾客手机号"
          v-model="l_phone"
        />
      </label>
      <div
        class="sendInfo"
        @click="btnSend"
      >确定</div>
    </mt-popup>
    <!-- 收款账户 -->
    <van-popup
      style="width: 100%"
      v-model="payeeAccountIdValueShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirm"
        @cancel="payeeAccountIdValueShow = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { isPermission } from '@/utils/filters';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessOrderPayCode, GetBusinessOrder, PostBusinessOrderOfflinePay } from '@/api/api_business_hotel_and_scale';
import { GetBusinessHotelPayeeAccountByhotel } from '@/api/api_business';
export default {
    data() {
        return {
            fore: ['在线支付房费和押金', '用户自主退房', '支付后获得开门密码', '免佣金'],
            pics: [{ pic: require('@/assets/two/weixin.png') }, { pic: require('@/assets/two/zhifubao.png') }],
            info: '',
            users: [],
            show_payment_outline: false,
            payeeAccountIdValueShow: false,
            payMentAll: [],
            top_pic: [{ pic: require('@/assets/two/weix.png') }, { pic: require('@/assets/two/zhifb.png') }],
            l_phone: '',
            abc: '',
            pic_code: '',
            price_show: false,
            statusInfo: '',
            active: 0,
            orderSourceMap: null,
            payeeAccountId: null,
            payeeAccountIdValue: null,
            is_businessPayOffline: true
        };
    },
    mounted() {
        this.timer = setInterval(this.getStatu, 10000);
    },
    beforeDestroy() {
        clearInterval(this.timer);
    },
    computed: {
        comWidth() {
            return window.innerWidth / 2.4 + 'px';
        }
    },
    created() {
        this.$store.commit('setHeader', '分房成功');
        this.is_businessPayOffline = isPermission('business:pay:offline');
        this.getStatu();
        this.info = JSON.parse(localStorage.getItem('fenfangInfo'));
        this.getCode();
    },
    methods: {
        // 获取手动分房付款二维码
        getCode() {
            let orderId = JSON.parse(localStorage.getItem('fenfangInfo')).orderId;
            GetBusinessOrderPayCode(orderId).done((res) => {
                this.pic_code = 'data:image/png;base64,' + res.data;
                if (res.order.orderAddSource == 2) {
                    this.getOrderSouce();
                }
            });
        },
        // 根据id获取订单
        getStatu() {
            let orderId = JSON.parse(localStorage.getItem('fenfangInfo')).orderId;
            GetBusinessOrder(orderId).done((res) => {
                this.statusInfo = res.data;
                if (this.statusInfo.payment != 0 || this.statusInfo.orderAddSource == 2) {
                    clearInterval(this.timer);
                }
            });
        },
        getOrderSouce() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                this.orderSourceMap = orderSourceMap;
            });
        },
        // 展开
        opened() {
            this.price_show = !this.price_show;
        },
        // 已在线下收款
        goOutLine() {
            this.getPayment();
            this.l_phone = this.statusInfo.phone;
            this.payeeAccountId = '';
        },
        // 支付方式
        getPayment() {
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.statusInfo.hotelId }).done((res) => {
                this.payMentAll = res.data.filter((item) => item.payment >= '5' || item.account);
            });
            if (this.statusInfo.accountUnitId && this.statusInfo.accountUnitId > 0) {
                GetBusinessHotelPayeeAccountByhotel({ hotelId: this.statusInfo.hotelId }).done((res) => {
                    if (res.data.length > 0) {
                        this.userAll = res.data.filter((item) => item.payment >= '5' || item.account);
                        this.users = res.data.filter((item) => item.payment >= '5' || item.account).map((item) => item.accountName);
                        this.payeeAccountId = this.userAll[0].id;
                        this.payeeAccountIdValue = this.userAll[0].accountName + ' ' + '>';
                    }
                    this.show_payment_outline = true;
                });
            } else {
                this.show_payment_outline = true;
            }
        },
        // 选择收款账户
        onConfirm(val) {
            this.payeeAccountIdValue = val + ' ' + '>';
            this.payeeAccountId = this.userAll.find((item) => item.accountName == val).id;
            this.payeeAccountIdValueShow = false;
        },
        // 选择收款方式
        choose_payment(item, i) {
            this.active = i;
            this.payeeAccountId = item.id;
        },
        // 发送短信
        btnSend() {
            if (!this.payeeAccountId) {
                Toast({
                    message: '请正确选择线下收款方式',
                    duration: 900
                });
                return false;
            }
            if (this.l_phone && !/^1[3-9]\d{9}$/.test(this.l_phone)) {
                Toast({
                    message: '请正确输入手机号',
                    duration: 900
                });
                this.l_phone = '';
                return false;
            }
            let data = {
                phone: this.l_phone,
                payeeAccountId: this.payeeAccountId
            };
            let orderId = this.statusInfo.id;
            PostBusinessOrderOfflinePay(orderId, data).done((res) => {
                this.show_payment_outline = false;
                Toast({
                    message: res.msg,
                    duration: 1000
                });
                this.statusInfo.orderAddSource = res.data.orderAddSource;
                this.statusInfo.payment = res.data.payment;
                clearInterval(this.timer);
            });
        },
        // 返回首页
        goBackIndex() {
            // this.$router.push('/dashboard');
            // debugger
            this.$store.commit({
                type: 'm_getOrder',
                m_queryValue: '',
                m_statu: '2'
            });
            this.$router.push({
                path: '/order',
                query: {
                    hotelId: this.statusInfo.hotelId
                }
            });
            this.c_hotelAll = JSON.parse(localStorage.getItem('c_hotelAll'));
            this.c_hotelId = JSON.parse(localStorage.getItem('c_hotelId'));
            this.role = JSON.parse(localStorage.getItem('roles'))[0].name;
            this.hotelName = this.c_hotelAll.find((item) => item.id == this.c_hotelId).hotelName;
            let obj = {
                c_hotelId: this.c_hotelId,
                hotelName: this.hotelName,
                role: this.role,
                yesOrNo: 'yes'
            };
            localStorage.setItem('isSdff', JSON.stringify(obj));
        },
        // 发送短信-返回上一页
        goBack() {
            this.abc = '';
            this.getStatu();
        }
    }
};
</script>
<style scoped>
.f9 {
    font-size: 15px;
    font-weight: bold;
}
#colorPay:hover {
    background-color: rgba(255, 125, 65, 1);
    color: #fff;
    opacity: 0.95;
    border-radius: 5px;
}
#big_box {
    position: fixed;
    top: 60px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
#customer {
    margin: 10px 10px 0;
    border-radius: 5px;
    opacity: 0.9;
    color: #fff;
    line-height: 50px;
    text-align-last: justify;
    padding: 0 40px;
    font-size: 16px;
}
.bgf60 {
    background: linear-gradient(270deg, rgba(255, 125, 65, 1) 0%, rgba(255, 142, 90, 1) 100%);
}
.bggreen {
    background: #02bc6c;
}
.bgblue {
    background: #2e7bf7;
}
#code {
    position: relative;
    border-radius: 5px;
    margin: 0 10px;
    background: #fff;
    padding-bottom: 6px;
    border-bottom: 1px solid #e5e5e5;
}
#code > div > span {
    font-size: 14px;
}
.w80 {
    width: 118px;
    height: 40px;
}
#code > div {
    display: flex;
    justify-content: center;
}
#code > div > div > img {
    width: 90px;
    height: 30px;
}
#ul {
    border-radius: 5px;
    margin: 0 10px;
    padding-top: 10px;
    background: #fff;
    font-size: 14px;
    color: #b2b2b2;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
#ul > div {
    line-height: 26px;
    text-align: left;
}
#ul > div > span > .span {
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 10px;
    background: #939393;
}
#backIndex {
    display: flex;
    justify-content: center;
    margin: 10px;
}
#backIndex > div {
    font-size: 15px;
    width: 50%;
    height: 40px;
    line-height: 40px;
    color: #6a6a6a;
}
#backIndex > div:nth-child(1) {
    background: #fff;
    border-radius: 5px;
}
#backIndex > div:nth-child(2) {
    background: #2e7bf7;
    margin-left: 10px;
    border-radius: 5px;
    color: #fff;
}
#backIndex > div:nth-child(2) > img {
    width: 20px;
    height: 20px;
    vertical-align: middle;
}
.c_0 {
    color: #000;
}
.c_f60 {
    color: #ff7d41;
    display: flex;
}

#top {
    font-weight: bold;
    color: #ff7d41;
    font-size: 18px;
    text-align: left;
    padding: 20px 10px;
}
#top > div > span {
    color: #000;
    font-size: 15px;
}
#top > span {
    color: #888;
    font-size: 14px;
}
#center > .roomInfo {
    text-align: left;
    padding: 4px 30px;
    font-size: 14px;
    font-weight: bold;
}
#center > .c_top {
    margin: 10px 10px 0;
    background: #fff;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    color: #000;
    font-size: 12px;
    display: flex;
    justify-content: space-around;
}
#center > .c_top > div {
    border: 1px dashed #b2b2b2;
    margin: 10px;
}
#center > .c_top > div > img {
    width: 150px;
    height: 150px;
}
#center > .c_top > div {
    text-align: left;
    margin: 20px;
}
#center > .c_top > div > div {
    align-items: center;
    display: flex;
}
#center > .c_top > div > div > img {
    width: 10px;
    height: 10px;
    margin-right: 6px;
}
#center > .c_top > div > section {
    margin-top: 20px;
    display: flex;
    align-items: center;
}
#center > .c_top > div > section > div {
    margin-right: 10px;
}
#center > .c_top > div > section > div > img {
    width: 30px;
    height: 30px;
}
#center > .c_top > div > section > div {
    color: #b2b2b2;
}
#center > .c_bottom {
    margin: 0 10px 10px;
    background: #fff;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    color: #888;
    font-size: 14px;
}
#center > .c_bottom > .c_b_box {
    display: flex;
    line-height: 30px;
    align-items: center;
}
#center > .c_bottom > .c_b_box > div:nth-child(1) {
    padding-left: 20px;
    width: 30%;
    text-align: left;
}
#center > .c_bottom > .c_b_box > div:nth-child(2) {
    width: 60%;
    text-align: left;
}
#button {
    border: 1px solid #ff7d41;
    background: #ff7d41;
    font-size: 14px;
    width: 90%;
    margin: 30px auto;
    color: #fff;
}
/* 分房成功 */
#success {
    background: #fff;
    margin: 50px 10px;
    padding-bottom: 20px;
    font-size: 14px;
    font-weight: bold;
}
#success > div:nth-child(1) {
    width: 50%;
    padding-top: 30px;
    margin: 30px auto;
}
#success > div:nth-child(2) {
    background: #ff7d41;
    color: #fff;
    line-height: 40px;
    width: 80%;
    border-radius: 5px;
    margin: 10px auto;
}
/* 线下收款 */
.outline {
    width: 90%;
    color: #000;
    background: #e7e8f1;
    border-radius: 5px;
    font-size: 14px;
}
.outline > div {
    margin: 10px;
    font-weight: bold;
}
.outline > span {
    font-size: 12px;
    color: #b2b2b2;
}
.outline > label {
    display: flex;
    margin: 10px auto;
    position: relative;
}
.outline > label > img {
    width: 12px;
    height: 20px;
    position: absolute;
    top: 35%;
    right: 90%;
}
.outline > label > input {
    text-indent: 30px;
    line-height: 46px;
    width: 100%;
    margin: 10px;
}
.outline > .pay {
    color: #b2b2b2;
    font-size: 12px;
    margin: 10px;
}
.pay_title {
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.pay_title > div {
    color: #000;
    background: #fff;
    margin-bottom: 10px;
    line-height: 20px;
    width: 30%;
    border-radius: 5px;
}
.sendInfo {
    width: 94%;
    background: rgba(255, 125, 65, 1);
    color: #fff;
    margin: 20px auto;
    line-height: 40px;
    border-radius: 5px;
}
.opened_box {
    border: 1px solid #f2f2f2;
}
.opened_box > div {
    font-size: 12px;
    display: flex;
    justify-content: space-evenly;
    line-height: 30px;
}
.opened_box > div > span {
    display: inline-block;
    text-align: left;
}
.user-style {
    display: flex;
    background: #fff;
    padding: 10px 8px;
}
.w30 {
    width: 30%;
    text-align: left;
}
</style>



